.header-nav{
    width: 100%;
    height: 120px;
    display: flex;
    
    box-shadow:0px 3px 3px #AEB4B2;

}

.header-nav .h-left{
    width: 28%;
    height: 120px;
    display: flex;
    align-items: center;
}

.header-nav .h-left img{
    max-width: 130px;
    height: auto;
    margin-left: 50px;
}

.header-nav .h-right{
    width: 70%;
    height: 120px;
    display: flex;
    justify-content: space-around;
    align-items: center;
}

.header-nav .h-right .navbar{
    width: 80%;
    display: flex;
    justify-content: space-between;
}

.h-right .nav-header{
    color: rgb(0, 0, 0);
    font-size: 17px;
    text-decoration: none;
    font-weight: 500;
}

ul li{
    list-style-type: none;
}

a{
    text-decoration: none;
}

.h-right .nav-header:hover{
    color: rgb(185, 15, 15);
}

.h-right .navbar #hidden-menu{
    width: 200px;
    height: 200px;
    margin-top: 20px;
    position: absolute;
    z-index: 2;
    visibility: visible;
    background-color: white;
    box-shadow: 2px 4px 8px 2px rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}

.header-nav .h-right .navbar .sub-menu li{
    margin-top: 8px;
    padding: 10px;
}

.header-nav .h-right .navbar .sub-menu li .nav-submenu{
    text-decoration: none;
    font-weight: 500;
    color: black;
}

.header-nav .h-right .navbar .sub-menu li .nav-submenu:hover{
    color: rgb(185, 15, 15);

}
